import ProjectTile from './ProjectTile';
import calc from './img/js-calc-preview.png';
import wiki from './img/wiki-viewer-preview.png';

export default function Projects() {
  return (
    <section id="projects" className="p-2 flex flex-col flex-wrap h-full md:flex-row sm:content-center sm:items-center md:p-4 mb-12">
      <h1 className="text-lg flex-0 w-full text-5xl text-center my-6 my-6 md:my-36">Projects</h1>
      <ProjectTile name ="Javascript Calculator" descr="Calsulator app for FCC Front End Development Libraries Certification" img={calc} link="https://annestezia.github.io/fcc-calc/" />
      <ProjectTile name ="Wikipedia Viewer" descr="Wikipedia Viewer, Wikipedia API, search, get random article" img={wiki} link="https://annestezia.github.io/WikiViewer/" />
    </section>
  );
}