export default function ProjectTile({name,descr, link,img}) {
  return (
    <div className = "project-tile sm:w-full md:w-1/3   md:mr-6 mb-12  last:mr-0 p-4 shadow ">
      <a href={link} target="_blank">
        <figure>
          <img src={img} alt="Project Image" className=""/>
          <figcaption>
            <h3 className="text-xl">{name}</h3>
            <span>{descr}</span>
          </figcaption>
        </figure>        
      </a>     
    </div>
  );
}